<template>
  <loading v-if="!loaded"/>
  <div class="form-elements" v-else>
    <div class="row">
      <div class="col-md-12">
        <vuestic-widget :headerText="'menu.departments.update' | translate">
          <div class="row">
            <div class="col">
              <div class="row row-reverse">
                <div class="col-4">
                  <div class="row row-reverse">
                    <div class="col">
                      <div class="form-group with-icon-right"
                           :class="{'has-error': errors.has('title')}">
                        <div class="input-group">
                          <input
                            id="title"
                            name="title"
                            v-model="title"
                            v-validate="'required'"
                            required/>
                          <i
                            class="fa fa-exclamation-triangle icon-right input-icon"
                            v-show="errors.has('title')"></i>
                          <label class="control-label" for="title">{{'department.title'
                            | translate}}</label><i class="bar"></i>
                          <small v-show="errors.has('title')"
                                 class="help text-danger">{{
                            errors.first('title')
                            }}
                          </small>
                        </div>
                      </div>
                    </div>

                    <div class="col">
                      <div class="form-group with-icon-right"
                           :class="{'has-error': errors.has('slug')}">
                        <div class="input-group">
                          <input
                            id="slug"
                            name="slug"
                            v-model="slug"
                            v-validate="'required'"
                            required/>
                          <i
                            class="fa fa-exclamation-triangle icon-right input-icon"
                            v-show="errors.has('slug')"></i>
                          <label class="control-label" for="slug">{{'page.slug'
                            | translate}}</label><i class="bar"></i>
                          <small v-show="errors.has('slug')"
                                 class="help text-danger">{{
                            errors.first('slug')
                            }}
                          </small>
                        </div>
                      </div>
                    </div>

                    <div class="col">
                      <div class="form-group with-icon-right"
                           :class="{'has-error': errors.has('sort')}">
                        <div class="input-group">
                          <input
                            id="sort"
                            name="sort"
                            v-model="sort"
                            type="number"
                            v-validate="'required'"
                            required/>
                          <i
                            class="fa fa-exclamation-triangle icon-right input-icon"
                            v-show="errors.has('sort')"></i>
                          <label class="control-label" for="sort">{{'department.sort'
                            | translate}}</label><i class="bar"></i>
                          <small v-show="errors.has('sort')"
                                 class="help text-danger">{{
                            errors.first('sort')
                            }}
                          </small>
                        </div>
                      </div>
                    </div>
                  </div>
                  <div class="row">
                    <div class="col">
                      <textarea 
                        id="clinics"
                        class="col-md-12"
                        v-model="clinics"
                        :placeholder="'department.clinics' | translate"
                        style="min-height: 100px;"
                      >
                      </textarea>
                    </div>
                  </div>
                </div>
                <div class="col-8">
                  <div class="row">
                    <div class="col">
                      <Uploader
                        :maxFiles="1"
                        :placeholder="'department.image' | translate"
                        :images="getImage"
                        :apiUrl="imageApiUrl"
                        @onChange="reInitImages"
                      />
                    </div>
                    <div class="col">
                      <Uploader
                        :maxFiles="1"
                        :placeholder="'department.cover' | translate"
                        :images="getCover"
                        :apiUrl="coverApiUrl"
                        @onChange="reInitImages"
                      />
                    </div>
                  </div>
                </div>
              </div>

              <div class="row">
                <div class="col mt-3 mb-5">
                  <Uploader
                    :apiUrl="apiUrl" 
                    :images="getImages"
                    @onChange="reInitImages"
                  />
                </div>
              </div>

              <div class="row mt-5">
                <Editor @editor="handleEditor"/>
              </div>
            </div>

          </div>

          <div class="row">
            <div class="col col text-center mt-4">
              <button class="btn btn-success"  @click="update">
                {{'buttons.update' | translate}}
              </button>
            </div>
          </div>
        </vuestic-widget>        
      </div>
    </div>
  </div>
</template>

<script>
  import api from 'src/packages/Api'
  import Loading from 'partials/Loading'
  import Editor from 'view/components/editor'
  import Uploader from 'view/components/uploader'
  
  export default {
    name: 'update-department',
    data () {
      return {
        department: {},
        title: '',
        slug: '',
        clinics: '',
        data: '',
        sort: '',

        editor: null,
        loaded: false,
        apiUrl: 'departments/' + this.$route.params.id + "/upload/images",
        coverApiUrl: 'departments/' + this.$route.params.id + "/upload/cover",
        imageApiUrl: 'departments/' + this.$route.params.id + "/upload/image"
      }
    },
    components: {
      Editor,
      Loading,
      Uploader
    },
    created () {
      api.get(`departments/${this.$route.params.id}`).then(response => {
        const res = response.data

        this.department = res
        this.id = res.id
        this.title = res.title
        this.slug = res.slug
        this.clinics = res.clinics
        this.data = res.data
        this.sort = res.sort

        this.loaded = true
      }).catch(error => {
        this.$store.dispatch('addToastMessage',
          { text: this.$t(`errors.${error.data.status_code}`) }
        )
        this.$router.push({ name: 'departments' })
      })
    },
    methods: {
      handleEditor (editor) {
        this.editor = editor

        this.editor.setContents(this.department.data)
      },

      getImages () {
        const images = this.department.images.data || []

        return images
      },

      getCover () {
        const cover = this.department.cover
          ? [this.department.cover.data] 
          : []

        return cover
      },

      getImage () {
        const image = this.department.image
          ? [this.department.image.data] 
          : []

        return image
      },

      reInitImages (response) {
        this.department = response.data
      },

      update () {
        this.$validator.validateAll().then((result) => {
          if (result) {
            const data = {
              title: this.title,              
              slug: this.slug,              
              clinics: this.clinics,
              data: this.editor.getContents(),
              sort: this.sort
            }

            api.put(`departments/${this.id}`, data).then(response => {
              this.$store.dispatch('addToastMessage',
                { text: this.$t('department.updated'), type: 'success' }
              )
              this.$router.push({ name: 'department', params: { id: response.data.id } })
            }).catch(error => {
              this.$store.dispatch('addToastMessage',
                { text: this.$t('errors.any'), error }
              )
            })
          }
        })
      }
    }
  }
</script>

